import { Link } from "react-router-dom";

export type petType = { type: string; link: string };
const pets: petType[] = [
	{ type: "dog", link: "https://wallpaperaccess.com/full/266770.jpg" },
	{
		type: "cat",
		link:
			"data:image/jpeg;base64,",
	},
	{
		type: "cow",
		link:
			"https://th.bing.com/th/id/OIP.HrAiTxQv-FqxEkB0k3tuAgHaE6?w=268&h=180&c=7&o=5&dpr=1.25&pid=1.7",
	},
	{
		type: "goat",
		link:
			"https://th.bing.com/th/id/R089ca11a590afa8ece6604a2b0fb714f?rik=m6FBQtOuW78qsQ&riu=http%3a%2f%2f2.bp.blogspot.com%2f-XK4ISxWQo9A%2fT_kep1D5M_I%2fAAAAAAAAAWA%2fvqYLFj1V1sc%2fs1600%2fDSC04562.JPG&ehk=S3Z1BxwDqkdsOtCh681elTSldwrJSg5WQL10xgN2kuM%3d&risl=&pid=ImgRaw",
	},
	{
		type: "bird",
		link:
			"https://www.thehindu.com/migration_catalog/article12270303.ece/ALTERNATES/LANDSCAPE_615/12_YT_SPARROW",
	},
];

export interface IPets {}

export const Pets: React.FC<IPets> = () => {
	const petsMap = pets.map(({ link, type }, idx) => (
		<div key={idx} className='col-lg-4 col-md-5 col-sm-12 text-center'>
			<div className='card m-2'>
				<img src={link} className='card-img-top' alt='...' />
				<div className='card-body'>
					<h5 className='card-title'>{type.toUpperCase()}</h5>
					<p className='card-text'>
						Some quick example text to build on the card title and make up the bulk of the card's
						content.
					</p>
					<span className='btn btn-primary btn-lg'>
						<Link to={`/petify/${type}`} style={{ color: "white" }}>
							Go somewhere
						</Link>
					</span>
				</div>
			</div>
		</div>
	));

	return (
		<div className='container'>
			<div className='row row-cols-3 mt-5'>{petsMap}</div>
		</div>
	);
};
